.footer {
	background-color: $dark-purple;
	position: relative;
	padding: 100px 0 120px;
	@include r(1023) {
		padding: 75px 0 50px; }
	&__section-title {
		margin-bottom: 85px;
		@include r(1023) {
			margin-bottom: 45px; } }
	&__content {
		display: flex;
		@include r(1023) {
			flex-direction: column-reverse; } }
	&__address-list {
		display: flex;
		margin-right: 130px;
		@include r(1023) {
			margin-right: 40px;
			margin-right: 0; }
		@include r(639) {
			flex-direction: column; }
		&:last-child {
			margin-right: 0;
			@include r(1023);
			margin-bottom: 60px; } }
	&__address-list-item {
		max-width: 210px;
		width: 100%;
		margin-right: 40px;
		padding-right: 10px;
		@include r(1023) {
			max-width: 250px;
			margin-right: 20px; }
		@include r(639) {
			margin-right: 0;
			margin-bottom: 30px;
			padding-right: 0;
			&:last-child {
				margin-bottom: 0; } }
		&:last-child {
			margin-right: 0; }
		.footer__address-line {
			display: none;
			@include r(1023) {
				display: block; } } }
	&__address-list-item-city,
	&__address-list-item-address {
		color: $white; }
	&__address-list-item-city {
		margin-bottom: 20px;
		@include pr;
		font-weight: 300;
		font-size: 33px;
		@include r(1023) {
			font-size: 30px; } }
	&__address-list-item-address {
		@include pl; }
	&__address-line {
		display: block;
		width: 75px;
		height: 1px;
		background-color: $yellow;
		margin-top: 60px;
		@include r(1023) {
			display: none; } }
	&__social-list {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		margin-top: 65px;
		margin-bottom: 150px;
		@include r(1023) {
			margin-bottom: 80px;
			flex-wrap: wrap; }
		@include r(639) {
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start; } }
	&__social-list-item {
		margin-right: 30px;
		@include r(1023) {
			margin-bottom: 20px; }
		@include r(639) {
			margin-right: 0; }
		&:last-child {
			margin-right: 0; } }
	&__social-list-item-link-icon {
		border: 1px solid $yellow;
		padding: 5px;
		border-radius: 50%;
		display: block;
		width: 30px;
		height: 30px;
		margin-right: 15px; }
	&__social-list-item-link-icon-inner {
		overflow: hidden;
		position: relative;
		display: block;
		width: 100%;
		height: 100%; }
	&__social-list-item-link {
		color: $yellow;
		display: inline-flex;
		align-items: center;
		justify-content: flex-start;
		@include pr;
		svg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 30px;
			height: 30px; }
		&:hover {
			svg {
				animation: arrowAnimation ease 1.2s infinite; } } }
	&__copyright {
		text-align: center;
		@include pl;
		color: $white; } }

@keyframes arrowAnimation {
	from {
		transform: translate(-50%, -50%);
		opacity: 1; }
	40% {
		transform: translate(-50%, -50%); }
	50% {
		opacity: 0; }
	60% {
		transform: translate(-35px, -50%);
		opacity: 0; }
	to {
		transform: translate(-50%, -50%);
		opacity: 1; } }

