@include font-face('Poppins-Thin', '../fonts/');
@include font-face('Poppins-Regular', '../fonts/');
@include font-face('Poppins-Medium', '../fonts/');

@mixin pl {
	font-family: Poppins-Thin; }

@mixin pr {
	font-family: Poppins-Regular; }

@mixin pm {
	font-family: Poppins-Medium; }

html {
	font-size: 16px;
	line-height: 1.25;
	color: $dark-purple; }

h1,h2,h3,h4,h5,h6,p {
	margin: 0;
	padding: 0; }

picture,
img {
	max-width: 100%;
	height: auto;
	display: block; }

img {
	&.lazy {
		background-image: url(data:image/gif;base64,R0lGODlhCgAIAIABAN3d3f///yH5BAEAAAEALAAAAAAKAAgAAAINjAOnyJv2oJOrVXrzKQA7); }
	&.loaded {
		background-image: none; } }

.text-yellow {
	color: $yellow; }

.text-headline-1 {
	font-size: 72px;
	line-height: 1.25;
	@include r(1919) {
		font-size: 64px; }
	@include r(1365) {
		font-size: 54px; }
	@include r(1023) {
		font-size: 30px; }
	@include r(374) {
		font-size: 26px; } }

.text-paragraph-1 {
	font-size: 33px;
	line-height: 1.25;
	letter-spacing: 0.5px;
	@include r(1023) {
		font-size: 22px; } }

.text-paragraph-2 {
	font-size: 16px;
	line-height: 1.45;
	letter-spacing: 0.5px; }
