.investment-focus {
	position: relative;
	padding: 100px 0 35px;
	min-height: 100vh;
	height: 100%;
	display: flex;
	align-items: stretch;
	@include r(1023) {
		height: auto;
		min-height: initial; }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 0;
		width: 100%;
		height: 100%;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba($dark-purple, .65); }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			display: block; } }
	&__content {
		width: 100%;
		// height: 100%
		position: relative;
		display: grid;
		grid-template-rows: auto 1fr; }
	&__content-header {
		margin-bottom: 40px;
		@include r(767) {
			margin-bottom: 60px; } }
	&__content-body {
		align-self: center; }
	&__body {
		display: flex;
		align-items: stretch;
		justify-content: flex-start; }
	&__navigation {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		margin-right: 100px;
		@include r(1365) {
			margin-right: 50px; }
		@include r(767) {
			display: none; } }
	&__navigation-item {
		border: 0;
		background-color: transparent;
		cursor: pointer;
		outline: none;
		color: $white;
		margin-bottom: 45px;
		@include r(1365) {
			margin-bottom: 35px; }
		.section-title__line {
			background-color: $white; }
		&.is-active {
			color: $yellow;
			.section-title__line {
				background-color: $yellow; } } }
	&__tile {
		position: relative;
		max-width: 1100px;
		width: 100%; }
	&__tile-item {
		padding: 50px 100px;
		position: absolute;
		top: 50%;
		left: 50%;
		max-width: 880px;
		width: 100%;
		height: 100%;
		transform: translate(-50%, -50%);
		pointer-events: none;
		@include r(1365) {
			padding: 50px;
			max-width: 750px; }
		@include r(767) {
			position: relative;
			top: auto;
			left: auto;
			transform: translate(0, 0);
			padding: 0;
			pointer-events: auto;
			height: auto;
			margin-bottom: 40px; }
		&.is-active {
			pointer-events: auto;
			.investment-focus__tile-item-inner {
				opacity: 1;
				transform: translateY(0); }
			.investment-focus__tile-item-title {
				@include r(767) {
					display: flex; } } } }
	&__tile-item-inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		transform: translateY(-50px);
		opacity: 0;
		transition: opacity $transition-time, transform $transition-time;
		@include r(767) {
			opacity: 1;
			transform: translateY(0); }
		&.is-active {
			opacity: 1;
			transform: translateY(0); } }
	&__tile-item-headline {
		color: $yellow;
		@include pr;
		font-weight: 400;
		margin-bottom: 15px; }
	&__tile-item-description {
		@include pl;
		color: $white;
		p {
			color: inherit;
			font-size: inherit;
			font-weight: inherit;
			letter-spacing: inherit;
			font-family: inherit; } } }
