.team {
	padding: 115px 0 0;
	background-color: $white;
	@include r(767) {
		padding: 115px 0 75px; }
	&__section-title {
		margin-bottom: 100px;
		@include r(767) {
			margin-bottom: 40px; } }
	&__content-section {
		display: flex;
		align-items: stretch;
		margin-bottom: 100px;
		min-height: 960px;
		@include r(1365) {
			min-height: 650px; }
		@include r(767) {
			flex-direction: column; }
		&:last-child {
			margin-bottom: 0; }
		&:nth-child(odd) {
			.team__content-block-picture {
				right: 0; }
			.team__content-block_body {
				padding-left: 95px;
				@include r(1023) {
					padding-left: 50px; }
				@include r(767) {
					padding-left: 0; } } }
		&:nth-child(even) {
			flex-direction: row-reverse;
			@include r(767) {
				flex-direction: column; }
			.team__content-block-picture {
				left: 0; }
			.team__content-block_body {
				padding-right: 95px;
				@include r(1023) {
					padding-right: 50px; }
				@include r(767) {
					padding-right: 0; } } } }
	&__content-block {
		width: 50%;
		@include r(767) {
			width: 100%; }
		&:last-child {
			margin-bottom: 0; } }
	&__content-block_media {
		position: relative;
		@include r(767) {
			margin-bottom: 30px; } }
	&__content-block_body {
		align-self: center;
		max-width: 650px;
		padding-bottom: 30px;
		@include r(767) {
			padding-bottom: 0; } }
	&__content-block-picture {
		position: absolute;
		z-index: 0;
		top: 0;
		width: 50vw;
		height: 100%;
		@include r(767) {
			position: relative;
			width: auto;
			height: 400px;
			margin: 0 -48px; }
		&::after {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: rgba($dark-purple, 0.79);
			mix-blend-mode: screen; }
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__content-block-title {
		@include pr;
		font-weight: 300; }
	&__content-block-position {
		color: $yellow;
		@include pr;
		font-weight: 400;
		margin-bottom: 35px; }
	&__content-block-line {
		display: block;
		width: 75px;
		height: 1px;
		background-color: $yellow;
		margin-bottom: 35px; }
	&__content-block-description {
		@include pr;
		font-weight: 400;
		margin-bottom: 60px;
		p {
			margin-bottom: 35px;
			&:last-child {
				margin-bottom: 0; } } }
	&__content-block-social {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		list-style: none; }
	&__content-block-social-item {
		margin-right: 23px;
		&:last-child {
			margin-right: 0; } }
	&__content-block-social-item-link {
		color: #C4C4C4;
		transition: color $transition-time;
		svg {
			width: 20px;
			height: 20px; }
		&:hover {
			color: $yellow; } } }
